// Help and Support Page

export default function HelpAndSupportPage() {
    return (
      <div className="max-w-4xl mx-auto p-6 space-y-6 bg-gray-50 rounded-lg shadow-md">
        <h2 className="text-2xl font-bold text-gray-800">Help and Support</h2>
  
        {/* Documentation Section */}
        <div className="space-y-4">
          <h3 className="text-lg font-semibold text-gray-800">Documentation</h3>
          <p className="text-gray-600">Find guides and tutorials to help you get started:</p>
          <ul className="list-disc list-inside space-y-2">
            <li><a href="/docs/getting-started" className="text-purple-600 hover:underline">Getting Started</a></li>
            <li><a href="/docs/account-setup" className="text-purple-600 hover:underline">Account Setup</a></li>
            <li><a href="/docs/troubleshooting" className="text-purple-600 hover:underline">Troubleshooting</a></li>
          </ul>
        </div>
  
        {/* Contact Support Section */}
        <div className="space-y-4">
          <h3 className="text-lg font-semibold text-gray-800">Contact Support</h3>
          <p className="text-gray-600">Need help? Reach out to our support team:</p>
          <a
            href="mailto:support@example.com"
            className="px-4 py-2 inline-block text-sm font-medium text-white bg-purple-600 rounded-lg hover:bg-purple-700"
          >
            Email Support
          </a>
        </div>
  
        {/* FAQ Section */}
        <div className="space-y-4">
          <h3 className="text-lg font-semibold text-gray-800">Frequently Asked Questions</h3>
          <ul className="space-y-3">
            <li>
              <details className="bg-gray-100 rounded-lg p-4 shadow">
                <summary className="cursor-pointer font-medium text-gray-800">How do I reset my password?</summary>
                <p className="text-sm text-gray-600 mt-2">Go to the login page, click "Forgot Password," and follow the instructions.</p>
              </details>
            </li>
            <li>
              <details className="bg-gray-100 rounded-lg p-4 shadow">
                <summary className="cursor-pointer font-medium text-gray-800">Where can I view my order history?</summary>
                <p className="text-sm text-gray-600 mt-2">Navigate to the "My Orders" section in your account dashboard.</p>
              </details>
            </li>
            <li>
              <details className="bg-gray-100 rounded-lg p-4 shadow">
                <summary className="cursor-pointer font-medium text-gray-800">How do I contact customer support?</summary>
                <p className="text-sm text-gray-600 mt-2">You can email us at support@example.com or use the live chat feature on our website.</p>
              </details>
            </li>
          </ul>
        </div>
      </div>
    );
  }
  